import * as React from "react"
import AwardGold from "../images/award_gold.svg"
import AwardSilver from "../images/award_silver.svg"
import AwardBronze from "../images/award_bronze.svg"
import AwardMedalion from "../images/award_medalion.svg"
import AwardNone from "../images/award_none.svg"
import AwardInvited from "../images/award_invited.svg"

const Participant = ({ participant }) => {
  const award = {
    1: AwardGold,
    2: AwardSilver,
    3: AwardBronze,
    4: AwardMedalion,
    0: AwardNone,
    "-1": AwardInvited,
  }
  return (
    <div className="flex gap-4 items-center bg-white border-2 rounded-lg drop-shadow-xl p-6 w-full hover:-translate-y-2 transition-transform">
      <img
        src={
          award.hasOwnProperty(participant.place)
            ? award[participant.place]
            : award[0]
        }
        alt=""
        className="max-h-24"
      />
      <div>
        <h4 className="text-xl font-semibold">{participant.name}</h4>
        <p className="text-sm">
          {`${participant.skill}${
            participant.junior ? "J" : participant.youth ? "Y" : ""
          } ${participant.skill_name}${
            participant.junior || participant.youth ? " (юниоры)" : ""
          }`}
        </p>
      </div>
    </div>
  )
}

export default Participant
